import { Component, OnInit } from '@angular/core';
import { MenuperfilService } from '../menuperfil.service';
import { Ng4LoadingSpinnerService } from 'ng4-loading-spinner';
import { IncidenciaService } from '../../../incidencias/incidencia.service';
import { Router } from '@angular/router';
import { NotificationService } from '../../../shared';
import { PerfilService } from '@app/services/perfil/perfil.service';

@Component({
  selector: 'app-menuperfil-index',
  templateUrl: './menuperfil-index.component.html',
  styleUrls: ['./menuperfil-index.component.css']
})
export class MenuperfilIndexComponent implements OnInit {
  perfil: string;
  lista_item: any;
  perfiles: any;
  constructor(private menuperfilServicm: MenuperfilService,
    private spinnerService: Ng4LoadingSpinnerService,
    private incidenciaService: IncidenciaService,
    private router: Router,
    private notificationService: NotificationService,
    private perfilService:PerfilService, ) {
    this.perfil = '-1';
    this.getPerfiles();
  }

  ngOnInit() {

  }
  cargarmenu(opcion) {
    this.spinnerService.show();
    this.menuperfilServicm.get(opcion).subscribe((value) => {
      this.lista_item = value.data;
      this.spinnerService.hide();
    });

  }
  getPerfiles() {
    this.perfilService.get(1).subscribe((value) => {
      this.perfiles = value.data;

    });

  }
  selectperfil(event) {
    this.perfil = event.target.value;
    this.cargarmenu(event.target.value);
  }
  actualizarmenu(event, row) {
    row.selected = event.target.checked;

    row.perfil = this.perfil;
    this.menuperfilServicm.add(row)
      .subscribe((value) => {

        if (row.selected) {
          row.menu_perfil_id = value.data.id;

        } else {
          row.menu_perfil_id = null;
        }

      }, (err) => {
        this.spinnerService.hide();
        if (err.status_code == 422) {
          this.notificationService.onError('El Contrato ya existe');
        } else {
          this.notificationService.onError('Se ha producido un error.');
        }
      });
  }
}



